.footer {
    max-width: 880px;
    margin: 0 auto;
    padding: 66px 0 60px 0;
}

@media screen and (max-width: 900px) {
    .footer {
        padding: 66px 19px 40px 19px;
    }
}

@media screen and (max-width: 680px) {
    .footer {
        width: 100%;
        padding: 48px 0 36px 0;
    }
}