.profile__name {
    font-family: Inter, Arial, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #FFFFFF;
    padding: 0;
    margin: 0 18px 0 0;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 680px) {
    .profile__name {
        max-width: 260px;
        font-size: 27px;
        line-height: 33px;
        text-align: center;
    }
}
